<!--  DOM  -->
<template>
    <div class="affairs">
        <!--  -->
        <v-ser :iszb="true" @searchFn="searchFn"></v-ser>
        <div class="box">
            <!-- list -->
            <van-pull-refresh v-model="loading" @refresh="onRefresh">
                <van-list finished-text="">
                    <v-con :type="'zb'" v-for="(el,i) in bidList" :key="i" :obj='el' ></v-con>
                </van-list>
            </van-pull-refresh>
        </div>
        
    </div>
</template>
<script>
import listCon from '@/components/enterprise/listCon'
import search from '@/components/enterprise/search'
export default {
    name: 'affairs', // 
    data() {
        return {
            // 
            loading:false,
            bidList:[],
            data:{
                pageNo:1,
                pageSize:5,  //
                type:2,
                regions:'',  //
                com_name:'',  //
                title:'',
                sumType:'zhongbiao'
            }
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    components:{
        'v-con':listCon,
        'v-ser':search
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        this.data.com_name = this.$route.query.name;
        this.data.regions=this.$route.query.source;
        this.ajax();
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        searchFn(option){//
            this.bidList=[];
            this.data.title=option;
            this.ajax();
        },
        onLoad(){
            this.data.pageNo++;
            this.ajax();
        },
        onRefresh(){
            this.bidList=[];
            setTimeout(() => {
                this.data.pageNo=1;
                this.ajax();
            }, 500);
        },
        ajax(){
            let that=this;
            this.$http({
                method:'post',
                url: '/notice/queryList',
                data:that.data
            }).then(function(res){
                if(that.bidList.length==0||that.data.pageNo==1){
                    that.bidList=res.data.data;
                }else{
                    that.bidList=that.bidList.concat(res.data.data)
                }
            })
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
.box{
    background: #f5f5f5;
    padding: 18px 32px;
    
}
</style>



